import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import storage from "./storage";
// 按需引入Ant Design of Vue组件库
import { Button, Tag, Icon, Radio, Timeline, Spin, Row, Col, Message, Anchor, BackTop, Modal, Tabs, Form, Input, Avatar, Space, Drawer, Menu } from "ant-design-vue";
// 引入全局组件
import Header from "./components/Header";
import Footer from "./components/Footer";
// 引入animate.css动画库
import animated from "animate.css";
// 引入qs
import qs from "qs";

Vue.prototype.$storage = storage;
Vue.prototype.$message = Message;
Vue.prototype.$qs = qs;

// 使用Ant Design of Vue组件库
Vue.use(Button).use(Tag).use(Icon).use(Radio).use(Timeline).use(Spin).use(Row).use(Col).use(Anchor).use(BackTop).use(Modal).use(Tabs).use(Form).use(Input).use(Avatar).use(Space).use(Drawer).use(Menu);
// 注册全局组件
Vue.component("Header", Header);
Vue.component("Footer", Footer);
Vue.use(animated);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
