<template>
  <div class="header">
    <div class="container animated fadeIn">
      <div class="left">
        <!-- Logo -->
        <div class="logo">
          <a href="javascript:;" @click="$router.push('/')"><img src="images/logo.png" alt="logo" /></a>
        </div>
        <!-- 导航栏 -->
        <div class="nav">
          <ul class="clearfix">
            <li>
              <router-link class="link" to="/course">课程<a-icon class="icon" theme="filled" type="fire" /></router-link>
            </li>
            <li>
              <router-link class="link" to="/forum">论坛</router-link>
            </li>
            <li>
              <router-link class="link" to="/menu">导航</router-link>
            </li>
          </ul>
        </div>
      </div>
      <!-- 用户栏 -->
      <div class="user">
        <!-- 登陆按钮 -->
        <div>
          <a-button type="primary" size="large" icon="user" @click="openModal">登录</a-button>
        </div>
      </div>
    </div>
    <a-modal v-model="visible" title="欢迎使用Madman" :footer="null" wrapClassName="headerModal" :maskClosable="!spinning">
      <a-spin :spinning="spinning" size="large">
        <!-- Logo -->
        <a-row style="width: 100%; margin-bottom: 20px" type="flex" justify="center" align="middle">
          <a-col><img style="width: 80px; height: 80px" src="../../public/images/img2.png" alt="" /></a-col>
          <a-col> <img src="../../public/images/img1.png" alt="" /></a-col>
        </a-row>
        <!-- 登录注册按钮 -->
        <a-radio-group v-model="active" button-style="solid" size="large">
          <a-radio-button value="login"> 登录 </a-radio-button>
          <a-radio-button value="register"> 注册 </a-radio-button>
        </a-radio-group>
        <!-- 登录----------------------------------------------------------------------------------------------- -->
        <div v-show="active === 'login'" class="login animated flipInY">
          <!-- 表单 -->
          <a-form ref="login" :form="form_login" @submit="handleLogin">
            <!-- 用户名 -->
            <a-form-item>
              <a-input
                size="large"
                v-decorator="[
                  'userName',
                  {
                    rules: [{ required: true, message: '请输入用户名!' }],
                  },
                ]"
                placeholder="请输入用户名..."
                :maxLength="18"
              >
                <a-icon slot="prefix" type="user" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-item>
            <!-- 密码 -->
            <a-form-item>
              <a-input
                size="large"
                v-decorator="[
                  'password',
                  {
                    rules: [{ required: true, message: '请输入密码!' }],
                  },
                ]"
                type="password"
                placeholder="请输入密码..."
                :maxLength="18"
              >
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-item>
            <a-form-item>
              <!-- 滑动验证码 -->
              <drag-verify-img-chip ref="dragVerify" imgsrc="images/b3.jpg" :width="472" :isPassing="isPassing" :showRefresh="true" text="请按住滑块拖动" successText="验证通过" handlerIcon="arrow-icon" successIcon="success-icon" @refresh="$refs.dragVerify.reset()" @passcallback="pass" @passfail="$refs.dragVerify.reset()" successTip="验证通过"> </drag-verify-img-chip>
              <a-button style="margin-top: 24px" type="primary" html-type="submit" size="large" block>登录</a-button>
            </a-form-item>
          </a-form>
          <p style="text-align: center; margin-top: 30px">登录即代表你同意<span class="user-agreement" @click="toUserAgreement">用户协议</span>和<span class="privacy-policy" @click="toPrivacyPolicy">隐私政策</span></p>
        </div>
        <!-- 注册----------------------------------------------------------------------------------------------- -->
        <div v-show="active === 'register'" class="register animated flipInY">
          <!-- 表单 -->
          <a-form ref="register" :form="form_register" @submit="handleRegister">
            <!-- 手机号 -->
            <a-form-item>
              <a-input
                size="large"
                v-decorator="[
                  'phone',
                  {
                    rules: [{ required: true, message: '请输入手机号!' }],
                  },
                ]"
                placeholder="请输入手机号..."
              >
                <a-icon slot="prefix" type="phone" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-item>
            <!-- 密码 -->
            <a-form-item>
              <a-input
                size="large"
                v-decorator="[
                  'password',
                  {
                    rules: [{ required: true, message: '请输入密码!' }, { validator: validateToNextPassword }],
                  },
                ]"
                type="password"
                placeholder="请输入密码..."
                :maxLength="18"
              >
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-item>
            <!-- 确认密码 -->
            <a-form-item>
              <a-input
                size="large"
                v-decorator="[
                  'confirmpassword',
                  {
                    rules: [{ required: true, message: '请再次输入密码!' }, { validator: compareToFirstPassword }],
                  },
                ]"
                type="password"
                placeholder="请再次输入密码..."
                :maxLength="18"
              >
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-item>
            <!-- 验证码 -->
            <a-form-item>
              <a-input-search
                size="large"
                @search="onSendCode"
                v-decorator="[
                  'code',
                  {
                    rules: [{ required: true, message: '请输入验证码!' }],
                  },
                ]"
                type="user"
                placeholder="请输入验证码..."
                :maxLength="8"
              >
                <a-button type="primary" slot="enterButton" :disabled="$store.state.codeTime != 0"> 发送验证码{{ $store.state.codeTime === 0 ? "" : "(" + $store.state.codeTime + ")" }} </a-button>
                <a-icon slot="prefix" type="message" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input-search>
            </a-form-item>
            <a-form-item>
              <a-button type="primary" html-type="submit" size="large" block> 注册 </a-button>
            </a-form-item>
          </a-form>
          <p style="text-align: center; margin-top: 30px">注册即代表你同意<span class="user-agreement" @click="toUserAgreement">用户协议</span>和<span class="privacy-policy" @click="toPrivacyPolicy">隐私政策</span></p>
        </div></a-spin
      >
    </a-modal>
  </div>
</template>

<script>
import dragVerifyImgChip from "vue-drag-verify-img-chip";
import { getVerificationCode, register, login, findUserByMobile } from "../api/user";

export default {
  name: "Header",
  data() {
    return {
      visible: false,
      active: "login",
      form_login: this.$form.createForm(this),
      form_register: this.$form.createForm(this),
      isPassing: false,
      spinning: false,
    };
  },
  methods: {
    // 打开登录注册弹框
    openModal() {
      this.isPassing = false;
      this.active = "login";
      this.visible = true;
      this.$nextTick(() => {
        this.form_login.resetFields();
        this.form_register.resetFields();
        this.$refs.dragVerify.reset();
      });
    },
    // 登录
    handleLogin(e) {
      e.preventDefault();
      this.form_login.validateFields((err, values) => {
        // 验证成功
        if (!err) {
          if (!this.isPassing) {
            this.$message.error("大哥！这么大一块验证码看不到吗？盘它！！！！！！");
            return;
          }
          this.spinning = true;
          window.setTimeout(
            function () {
              this.visible = false;
              this.$message.success("登录成功！");
              this.$router.push("/user");
            }.bind(this),
            2000
          );
          console.log(values);
        }
      });
    },
    // 注册
    handleRegister(e) {
      e.preventDefault();
      this.form_register.validateFields((err, values) => {
        // 验证成功
        if (!err) {
          if (values.password.length < 6 || values.password.length > 18) {
            this.$message.error({ content: "密码长度须为6至18位！请重新填写", key: "header" });
            return;
          }
          this.spinning = true;
          // 执行注册
          register({ phone: values.phone, password: values.password, verificationCode: values.code })
            .then((data) => {
              console.log("data" + data);
              if (data) {
                // 注册成功
                this.$message.success({ content: "注册成功！正在登录", key: "header" });
                // 执行登录
                login({ phone: values.phone, password: values.password })
                  .then((data) => {
                    // 登录成功
                    if (data) {
                      localStorage.setItem("token", data);
                      this.$message.success({ content: "登录成功！" });
                      // 执行获取用户信息
                      findUserByMobile({ phone: values.phone }).then((data) => {
                        if (data) {
                          this.$router.push("/user/index");
                        }
                      });
                    } else {
                      // 登录失败
                      this.$message.error({ content: "登录失败！请稍后再试", key: "header" });
                      this.spinning = true;
                    }
                  })
                  .catch(() => {
                    // 登录失败
                    this.$message.error({ content: "登录失败！请稍后再试", key: "header" });
                    this.spinning = true;
                  });
              } else {
                // 注册失败
                this.$message.error({ content: "注册失败！请稍后再试", key: "header" });
                this.spinning = true;
              }
            })
            .catch((err) => {
              console.log(err);
              // 注册失败
              this.$message.error({ content: "注册失败！请稍后再试", key: "header" });
              this.spinning = true;
            });
        }
      });
    },
    // 发送验证码
    onSendCode() {
      const phone = this.form_register.getFieldValue("phone");
      // 验证手机号是否符合格式
      if (!new RegExp(/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/).test(phone)) {
        // 不符合格式
        this.$message.error({ content: "请输入正确的手机号码！", key: "header" });
        return;
      }
      // 注册
      this.spinning = true;
      getVerificationCode(phone)
        .then(() => {
          this.$message.success({ content: "发送成功！请注意查收短信", key: "header" });
          // 限制发送验证码时间
          this.$store.state.codeTime = 120;
          // 开启定时器
          let code = window.setInterval(
            function () {
              if (this.$store.state.codeTime > 0) this.$store.state.codeTime--;
              else window.clearInterval(code);
            }.bind(this),
            1000
          );
          this.spinning = false;
        })
        .catch(() => {
          this.$message.error({
            content: "服务器繁忙！请稍后再试",
            key: "header",
          });
          this.spinning = false;
        });
    },
    // 跳转到用户协议页面
    toUserAgreement() {
      this.$message.success({
        content: "哥您就放一百个心吧！我们这全是霸王条款",
        duration: 2,
        key: "header",
      });
    },
    // 跳转到隐私政策页面
    toPrivacyPolicy() {
      this.$message.success({
        content: "我们要坦诚相对！你觉得呢",
        duration: 2,
        key: "header",
      });
    },
    // 滑动验证通过
    pass() {
      this.isPassing = true;
    },
    // 密码效验
    validateToNextPassword(rule, value, callback) {
      const form = this.form_register;
      if (value && this.confirmDirty) {
        form.validateFields(["confirmpassword"], { force: true });
      }
      callback();
    },
    // 确认密码效验
    compareToFirstPassword(rule, value, callback) {
      const form = this.form_register;
      if (value && value !== form.getFieldValue("password")) {
        callback("两次输入的密码不一致!");
      } else {
        callback();
      }
    },
  },
  components: { dragVerifyImgChip },
  watch: {
    active() {
      this.isPassing = false;
      this.$nextTick(() => {
        this.$refs.dragVerify.reset();
      });
    },
  },
};
</script>

<style lang="less" scoped>
.header::v-deep {
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 7;
  user-select: none;
  > div {
    height: 70px;
    display: flex;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      ul {
        list-style: none;
        margin: 0;
        li {
          float: left;
          position: relative;
          .link {
            padding: 0 24px;
            display: block;
            color: @colorG;
            font-size: @fontH;
            font-weight: bold;
            line-height: 70px;
          }
          .icon {
            position: absolute;
            right: 12px;
            top: 16px;
            width: 20px;
            height: 20px;
            color: rgb(240, 20, 20);
          }
        }
      }
    }
    .user {
      display: flex;
      align-items: center;
    }
  }
  .router-link-exact-active.router-link-active {
    color: lightsalmon !important;
  }
}
</style>
