import Vue from "vue";
import VueRouter from "vue-router";
import { Message } from "ant-design-vue";

Vue.use(VueRouter);

const routes = [
  // 首页
  {
    path: "/",
    name: "Home",
    component: () => import("../views/Home.vue"),
    meta: {
      title: "首页",
    },
  },
  // 课程页
  {
    path: "/course",
    name: "course",
    component: () => import("../views/course.vue"),
    meta: {
      title: "课程",
    },
  },
  // 用户模块-------------------------------------------------------------------------------------------------------------
  {
    path: "/user",
    name: "user",
    component: () => import("../views/user.vue"),
    redirect: "/user/index",
    meta: {
      title: "用户",
    },
    children: [
      {
        path: "/user/index",
        name: "userIndex",
        component: () => import("../views/user/index.vue"),
        meta: {
          title: "用户",
        },
      },
      {
        path: "/user/forum",
        name: "userForum",
        component: () => import("../views/user/forum.vue"),
        meta: {
          title: "用户",
        },
      },
      {
        path: "/user/course",
        name: "userCourse",
        component: () => import("../views/user/course.vue"),
        meta: {
          title: "用户",
        },
      },
      {
        path: "/user/saySomeThing",
        name: "userSaySomeThing",
        component: () => import("../views/user/saySomeThing.vue"),
        meta: {
          title: "用户",
        },
      },
      {
        path: "/user/accompanyMe",
        name: "userAccompanyMe",
        component: () => import("../views/user/accompanyMe.vue"),
        meta: {
          title: "用户",
        },
      },
      {
        path: "/user/settings",
        name: "userSettings",
        component: () => import("../views/user/settings.vue"),
        meta: {
          title: "用户",
        },
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) document.title = to.meta.title + "-Madman"; // 修改浏览器标题
  if (
    [
      "Home",
      "course",
      "user",
      "userIndex",
      "userForum",
      "userCourse",
      "userSaySomeThing",
      "userAccompanyMe",
      "userSettings",
    ].indexOf(to.name) == -1
  )
    Message.warning({
      content: "正在开发！宝贝别急",
      key: "notPage",
    });
  else next();
});
// 取消替换重复路由记录警告
const originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch((err) => err);
};
// 取消跳转重复路由记录警告
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};

export default router;
