import request from "../utils/request";
import qs from "qs";

// 获取验证码（默认123456）
export const getVerificationCode = (mobile) =>
  request({
    url: "/api/user/getVerificationCode",
    methods: "post",
    data: qs.stringify({
      mobile, // 手机号码
    }),
  });

// 注册
export const register = ({ phone, password, verificationCode }) =>
  request({
    url: "/api/user/register",
    methods: "post",
    data: qs.stringify({
      phone, // 手机号码
      password, // 密码
      verificationCode, // 验证码
    }),
  });

// 登录
export const login = ({ phone, password }) =>
  request({
    url: "/api/user/login",
    methods: "post",
    data: qs.stringify({
      phone, // 手机号码
      password, // 密码
    }),
  });

// 根据手机号码查询用户信息
export const findUserByMobile = ({ mobile }) =>
  request({
    url: "/api/user/login",
    methods: "get",
    params: {
      mobile, // 手机号码
    },
  });
