const StorageKey = "course";
export default {
  getItem(key, module_name) {
    // 获取数据，key为键名，module_name为模块名
    let storage = this.getStorage();
    if (module_name) {
      let val = storage[module_name];
      if (val) return val[key];
      else return "";
    } else return storage[key];
  },
  setItem(key, value, module_name) {
    // 修改数据，key为键名，value为值，module_name为模块名
    let storage = this.getStorage();
    if (module_name) {
      let val = storage[module_name];
      if (!val) storage[module_name] = {};
      storage[module_name][key] = value;
    } else storage[key] = value;
    this.setStorage(storage);
  },
  removeItem(key, module_name) {
    // 清除数据，key为键名，module_name为模块名
    let storage = this.getStorage();
    if (module_name) {
      let val = storage[module_name];
      if (val) delete storage[module_name][key];
    } else delete storage[key];
    this.setStorage(storage);
  },
  getStorage() {
    // 获取Storage对象
    return JSON.parse(window.sessionStorage.getItem(StorageKey) || "{}");
  },
  setStorage(storage) {
    // 设置Storage对象
    window.sessionStorage.setItem(StorageKey, JSON.stringify(storage));
  },
};
